<script>
import { mapGetters } from 'vuex';
export default {
    name: 'personnelReport',
    data() {
        return {
            mothsNum: 5,
            currentdate: '',
            dayTravelList: [], // 本日出差
            weekTravelList: [], // 本周出差
            dayHolidayList: [], // 本日休假
            weekHolidayList: [], // 本周休假
            weekStartDate: '',
            weekEndDate: '',
            currentyear: '',
            personId: '',
            ssotokenStr: '',
            leaderRole: '',
            tjList: {},
            tjYearList: {},
            tjListLength: 0, // 报表统计json数据长度
            yearListLength: 0, // 年度报表统计json数据长度
            selectFlag: true,
            inTime: 0, // 当月为第几月
            moreWeekTra: false,
            moreWeekAsk: false
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    mounted() {
        let vm = this;
        document.setTitle('出差请假数据');
        vm.loadSwiper();
        vm.currentdate = vm.formatDate(new Date(), 'yyyy-MM-dd');
        // vm.currentdate = '2018-05-16';
        vm.currentyear = vm.formatDate(new Date(), 'yyyy');
        let today = new Date();
        vm.inTime = today.getMonth() + 1; // 本月是第几月（5月为5）
        if (['01062125', '01061517', '01251247'].includes(vm.userInfo.empcode)) {
            // 人事报表特定的人加权限 liaowen zhangjunfeng wushuijiong
            vm.personId = 'renhaiying';
            vm.ssotokenStr = '56066d3b924fefa422fb283f7fb10aa4L/wp-2q2be4ba91abfac23c670166a93ed550bf';
            vm.leaderRole = 1;
        } else if (vm.userInfo.empcode == '01061878') {
            // 吴水炯用时总裁的数据报表
            vm.personId = 'shiwenchao';
            vm.ssotokenStr = '34b0e88d9ea6abe57fcb7fca9df963abL/wp-2q7c16084c92355f82670166a93ed550bf';
            vm.leaderRole = 2;
        } else {
            vm.personId = vm.userInfo.userid;
            vm.ssotokenStr = `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`;
            vm.leaderRole = vm.userInfo.role;
        }
        vm.getDayTravel();
        vm.getDayHoliday();
        vm.getWeekDate();
        // vm.monthSummary(vm.currentdate);
    },
    methods: {
        getWeekDate() {
            let vm = this;
            var now = new Date(); // 当前日期
            let nowNum = now.getTime();
            var nowDayOfWeek = now.getDay(); // 今天本周的第几天
            if (nowDayOfWeek == 0) {
                let StartDate = new Date(nowNum - 6 * 24 * 60 * 60 * 1000);
                let EndDate = new Date(nowNum);
                vm.weekStartDate = vm.formatDate(StartDate, 'yyyy-MM-dd');
                vm.weekEndDate = vm.formatDate(EndDate, 'yyyy-MM-dd');
            } else {
                let StartDate = new Date(nowNum - (nowDayOfWeek - 1) * 24 * 60 * 60 * 1000);
                let EndDate = new Date(nowNum + (7 - nowDayOfWeek) * 24 * 60 * 60 * 1000);
                vm.weekStartDate = vm.formatDate(StartDate, 'yyyy-MM-dd');
                vm.weekEndDate = vm.formatDate(EndDate, 'yyyy-MM-dd');
            }
            console.log(vm.weekStartDate);
            console.log(vm.weekEndDate);
            vm.getWeekTravel();
            vm.getWeekHoliday();
        },
        // 本日出差
        getDayTravel() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/list', {
                'type': 'travelremark',
                'personID': vm.personId,
                'startTime': vm.currentdate,
                'endTime': vm.currentdate,
                'hierachy': 'dept',
                'leader': vm.leaderRole,
                'token': vm.ssotokenStr
            }, function (res) {
                vm.dayTravelList = res.jsonData;
            });
        },
        // 本周出差
        getWeekTravel() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/list', {
                'type': 'travelremark',
                'personID': vm.personId,
                'startTime': vm.weekStartDate,
                'endTime': vm.weekEndDate,
                'hierachy': 'dept',
                'leader': vm.leaderRole,
                'token': vm.ssotokenStr
            }, function (res) {
                vm.weekTravelList = res.jsonData;
            });
        },
        // 本日休假
        getDayHoliday() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/list', {
                'type': 'askforleave',
                'personID': vm.personId,
                'startTime': vm.currentdate,
                'endTime': vm.currentdate,
                'hierachy': 'dept',
                'leader': vm.leaderRole,
                'token': vm.ssotokenStr
            }, function (res) {
                vm.dayHolidayList = res.jsonData;
            });
        },
        // 本周休假
        getWeekHoliday() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/list', {
                'type': 'askforleave',
                'personID': vm.personId,
                'startTime': vm.weekStartDate,
                'endTime': vm.weekEndDate,
                'hierachy': 'dept',
                'leader': vm.leaderRole,
                'token': vm.ssotokenStr
            }, function (res) {
                vm.weekHolidayList = res.jsonData;
            });
        },
        // 报表统计
        monthSummary(date) {
            let vm = this;
            // vm.loadSwiper();
            vm.get(vm.conf.portalMobile + '/portal/report/monthSummary', {
                'type': 'travelremark',
                'personID': 'vm.userInfo.userid',
                'startTime': date,
                'endTime': date,
                'hierachy': 'dept',
                'Status': 'month',
                'leader': vm.userInfo.role,
                'token': vm.ssotokenStr
            }, function (res) {
                vm.tjList = res.jsonData;
                vm.tjListLength = Object.keys(vm.tjList).length;
            });
        },
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: vm.inTime - 1,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                })
            });
        },
        // 调到报表详情页
        toForm(mothsNum, userid) {
            // let vm = this;
            // console.log(mothsNum);
            // vm.$router.push('/attendance/' + mothsNum + '/' + userid);
        },
        // 查看年度
        selectYear() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/report/monthSummary', {
                'type': 'travelremark',
                'personID': 'vm.userInfo.userid',
                'startTime': vm.currentdate,
                'endTime': vm.currentdate,
                'hierachy': 'dept',
                'Status': 'year',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                vm.tjYearList = res.jsonData;
                vm.yearListLength = Object.keys(vm.tjYearList).length;
            });
        },
        getHolidayType(leavetype) {
            let arr = ['年休假', '病假', '事假', '婚假', '产前假', '产假', '生育看护假', '节育假', '丧假', '工伤假']
            return arr[leavetype - 1];
        }
    }
}
</script>
